/**
 * @description modal
 */
import React, { useState } from 'react';
import { Form, Button, Modal, Select, Slider, Spin, message } from 'antd';
import axios from '@/request/axiosConfig';
import api_warn from '@/request/api/api_warn';

const { Option } = Select;

const layout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 16 }
};

export default function createHostModal(props) {
  // state
  const [loading, setLoading] = useState(false);
  // 
  const [changeValue, setChangeValue] = useState({});

  const [form] = Form.useForm();

  // 
  const onFinish = async values => {
    await setLoading(true);
    const { cpuUsed, ramUsed, netWidthUsed, cDiskUsed } = values;
    console.log(values);

    const data = {
      setting: { ...values },
      hid: props.hid
    };
    console.log('data', data);

    // 
    const res = await axios({
      url: api_warn.warnSetting,
      method: 'post',
      data
    });

    if (res.data.success) {
      // 
      const { id: uid } = JSON.parse(localStorage.getItem('userInfo'));
      props.saveOperation(
        uid,
        '',
        JSON.stringify([res.data.hid]),
        `--CPU:${cpuUsed}%,RAM${ramUsed}%,:${netWidthUsed}%,C:${cDiskUsed}%`
      );
    }

    setTimeout(() => {
      setLoading(false);
    }, 500);

    // Modalmessage
    props.closeWarnModal();
    message.success(`${props.hid}`);
    // props.getAllHost(); // 
  };

  return (
    <Modal
      title=''
      onCancel={props.closeWarnModal}
      cancelText=''
      visible={props.warnVisible}
      footer={null}
    >
      <Spin size='large' tip='...' spinning={loading}>
        <Form
          form={form}
          {...layout}
          name='nest-messages'
          onFinish={onFinish}
          initialValues={{
            ramUsed: 90,
            cpuUsed: 90,
            netWidthUsed: 90,
            cDiskUsed: 90
          }}
        >
          {/* CPU */}
          <Form.Item
            name='cpuUsed'
            label='CPU'
            rules={[{ required: true }]}
          >
            <Slider
              tipFormatter={value => `${value}%`}
              min={50}
              max={100}
              tooltipPlacement='right'
            />
          </Form.Item>
          {/*  */}
          <Form.Item
            name='ramUsed'
            label=''
            rules={[{ required: true }]}
          >
            <Slider
              tipFormatter={value => `${value}%`}
              defaultValue={80}
              min={50}
              max={100}
              tooltipPlacement='right'
            />
          </Form.Item>

          {/*  */}
          <Form.Item
            name='netWidthUsed'
            label=''
            rules={[{ required: true }]}
          >
            <Slider
              tipFormatter={value => `${value}%`}
              max={100}
              tooltipPlacement='right'
              min={50}
            />
          </Form.Item>

          {/* C */}
          <Form.Item
            noStyle
            shouldUpdate={true}
            shouldUpdate={(prevValues, currentValues) => {
              return prevValues.system !== currentValues.system;
            }}
          >
            {({ getFieldValue }) => (
              <Form.Item
                name='cDiskUsed'
                label={
                  getFieldValue('system') !== 'CentOS' &&
                  getFieldValue('system') !== 'Ubuntu'
                    ? 'C'
                    : ''
                }
                rules={[{ required: true }]}
              >
                <Slider
                  tipFormatter={value => `${value}%`}
                  min={50}
                  max={100}
                  tooltipPlacement='right'
                />
              </Form.Item>
            )}
          </Form.Item>
          <Form.Item wrapperCol={{ ...layout.wrapperCol, offset: 8 }}>
            <Button
              style={{ borderRadius: '8px' }}
              type='primary'
              size='large'
              htmlType='submit'
            >
              
            </Button>
          </Form.Item>
        </Form>
      </Spin>
    </Modal>
  );
}
